.grid {
  text-align: justify !important;
  text-justify: distribute-all-lines;
  font-size: 0.1px;
	box-sizing: border-box;
	-moz-box-sizing: border-box;
}
.grid:after {
  content: "";
  display: inline-block;
  width: 100%;
}
.grid > * {
  vertical-align: top;
  display: inline-block;
  width: 100%;
  text-align: left;
}
.lt-ie8 .grid { zoom: 1; }
.lt-ie8 .grid > * { display: inline; }
[class*="push-"], [class*="pull-"] { position: relative; }

